@import "../base-components";

.container-share {
	width: 100%;
	max-width: 100%;
	margin: 0 auto;
	padding: 0 30px;
	box-sizing: border-box;
	position: relative;
	display: flex;
	flex-direction: column;

	&__social {
		order: 2;
		@include media-breakpoint-up(md) {
			order: 1;
			position: absolute;
			top: 0;
			left: 0px;
			bottom: 0;
			padding-top: 30px;
			box-sizing: border-box;
		}
	}
	.meks_ess {
		@include media-breakpoint-up(md) {
			flex-direction: column;
		}
	}

	.meks_ess.circle .meks_ess-item,
	.meks_ess.square .meks_ess-item,
	.meks_ess.transparent .meks_ess-item {
		line-height: 1;
		min-width: 28px;
		width: 28px;
		height: 28px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #d3d3d3 !important;

		&.socicon-facebook {
			align-items: flex-end;
			font-size: 24px;
		}
		&:hover {
			background: #5a7443 !important;
		}

		@include media-breakpoint-up(md) {
			margin: 0 0 14px 0 !important;
		}
	}

	@include media-breakpoint-up(md) {
		width: 848px;
		padding: 0 110px;
	}
}
